html,
body,
#root,
.protool {
  font: 14px "Century Gothic", Futura, sans-serif;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
}

.night-mode {
  background-color: #151515;
  color: #ababab;
}

.night-mode textarea {
  background-color: #aaa;
  color: #151515;
  border-color: #ababab;
  border-radius: 5px;
}

.night-mode button {
  background-color: #aaa;
  color: #151515;
  border-color: #ababab;
  border-radius: 5px;
}

.night-mode select {
  background-color: #ababab;
  color: #151515;
  border-color: #aaa;
  border-radius: 5px;
}

.night-mode input[type=text] {
  background-color: #aaa;
  color: #151515;
  border-color: #ababab;
  border-radius: 5px;
}

.proto-library {
  height: 100%;
  resize: horizontal;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: auto;
  min-width: 15rem;
}

.inner-proto-library {
  padding: 0px 10px 0px 10px;
}

main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.message-builder {
  width: 100%;
  height: 100%;
  overflow: auto;
  resize: vertical;
}

.inner-message-builder {
  padding-left: 10px;
}

.output {
  width: 100%;
  flex-grow: 1;
  min-height: 10rem;
  resize: vertical;
}

.inner-output {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  align-items: flex-end;
}

.io-controls {
  display: flex;
  flex-direction: column;
  align-content: flex-end;
  margin-left: 10px;
  margin-bottom: 10px;
}

.area {
  width: 100%;
  height: 98%;
  margin: 0px;
  padding: 0px;
  resize: none;
}

ol, ul {
  padding-left: 10px;
}

.invalid {
  background-color : #ff4444 !important;
}

button.tiny {
  padding: 0px
}
